define('socketry/routes/application', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    model: function () {
      return [];
    },
    activate: function () {
      this.controllerFor("application").subscribe();
    },
    deactivate: function () {
      this.controllerFor("application").unsubscribe();
    }
  });

});